*{
    margin: 0;
    padding:0;
    border: 0;
    outline: 0;
    box-sizing: border-box;
    list-style: none;
    text-decoration: none;
}

:root{
    --color-bg:#1f1f38;
    --color-bg-variant: #627F7A;
    --color-primary: #95b4ae;
    --color-primary-variant: #4B886A;
    --color-white: ###E8F3F1;
    --color-light: #95B4AE;

    --transition:all 400ms ease;
   
    --container-width-lg:75%;
    --container-width-md:86%;
    --container-width-ms:90%
}

html{
    scroll-behavior: smooth;
}

::-webkit-scrollbar{
    display: none;
}
body{
    font-family: 'Poppins', sans-serif;
    background: #1f1f38;
    background: var(--color-bg);
    /* color: var(--color-white); */
    line-height: 1.7;
    background-image: url(/Amany-s_-Portfolio/static/media/paper.9d3add40.jpg);
}
/* General style */

.container{
    width: 75%;
    width: var(--container-width-lg);
    margin: 0 auto;
}

h1,h2,h3,h4,h5{
    font-weight: 500;
}
h1{
    font-size: 2.5rem;
}
section{
    margin-top: 8rem;
    height: 100vh;
}
section > h2, section >h5,section >h3{
    text-align: center;
    color: #95B4AE;
    color: var(--color-light);
}
section > h2{
    color: #95b4ae;
    color: var(--color-primary);
    margin-bottom: 3rem;
}
.text-light{
    color: #95B4AE;
    color: var(--color-light);
}

a{
    color: #95b4ae;
    color: var(--color-primary);
    transition: all 400ms ease;
    transition: var(--transition);
}

a:hover{
    color: ###E8F3F1;
    color: var(--color-white);
}

.btn{
    width: -webkit-max-content;
    width: max-content;
    display: inline-block;
    color: #95b4ae;
    color: var(--color-primary);
    padding: 0.75rem 1.2rem;
    border-radius: 20px;
    cursor: pointer;
    border: 1px solid #95b4ae;
    border: 1px solid var(--color-primary);
    transition: all 400ms ease;
    transition: var(--transition);
    margin-left: 10px;
    
}
.btn:hover{
    color: white;
    background: #4B886A;
    box-shadow: 0 0 10px #4B886A, 0 0 40px #4B886A, 0 0 80px #4B886A;
    transition-delay: 0.1s;
}


.btn-primary{
    background: #95b4ae;
    background: var(--color-primary);
    color: white
}
img{
    display: block;
    width: 100%;
    object-fit: cover;
}

@media screen and (max-width:600px){
    .container{
        width: 86%;
        width: var(--container-width-md);
    }
    section > h2{
        margin-bottom: 2rem;
    }
}


