.about__container{
    display: grid;
    grid-template-columns: 35% 50%;
    gap: 15%;
}

.about__me{
    width: 100%;
    aspect-ratio: 1/1;
    border-radius: 2rem;
    background: linear-gradient(45deg,transparent,var(--color-primary),transparent);
    display: grid;
    place-items: center;
}

.about__me-image{
    border-radius: 7rem;
    overflow: hidden;
    transform: rotate(10deg);
    transition: var(--transition);
}
.about__me-image:hover{
    transform: rotate(0);
}
.about__cards{
    display: grid;
    grid-template-columns: repeat(3,1fr);
    gap: 1.5rem;
}
.about__card{
    background: #7FB6AC;
    border: 1px solid transparent;
    border-radius: 1rem;
    padding: 2rem;
    text-align: center;
    transition: var(--transition);

    color: #fff;
    
    box-shadow: 0 0 20px #FFE6FF, 0 0 20px #FFE6FF;
    transition-delay: 1s;

}
.about__card:hover{
    background: transparent;
    border-color: var(--color-primary-variant);

    cursor: default;
}
.about__icon{
    color: white;
    font-size: 1.4rem;
    margin-bottom: 1rem;
}

.about__card h5{
    font-size: 0.95rem;
    font-weight: bold;
    /* color: rgb(177, 169, 169); */
}

.about__card small{
    font-size: 0.7rem;
    color: rgb(241, 227, 227);
    /* color: var(--color-light); */
    font-weight: bold;
}

.about__content p{
    margin: 2rem 0 2.6rem;
    color: var(--color-light);
}
@media screen and (max-width:1024px){
    .about__container{
        grid-template-columns: 1fr;
        gap: 0;
    }
    .about__me{
        width: 50%;
        margin: 2rem auto 4rem;
    }
    .about__content p{
        margin: 1rem 1.5 1.5rem;
       
    }
}

@media screen and (max-width:600px){
   
    .about__me{
        width: 65%;
        margin: 0 auto 3rem;
    }
    .about__cards{
        grid-template-columns: 1fr 1fr;
        gap: 1rem;
    }
    .about__content {
        text-align: center;
       
    }
    .about__content p{
        margin: 1.5rem 0;
       
    }
    .a{
        margin-top: -80%;
    }
}