@import url('https://fonts.googleapis.com/css2?family=Poppins:wght@300;400;500;600&display=swap');
*{
    margin: 0;
    padding:0;
    border: 0;
    outline: 0;
    box-sizing: border-box;
    list-style: none;
    text-decoration: none;
}

:root{
    --color-bg:#1f1f38;
    --color-bg-variant: #627F7A;
    --color-primary: #95b4ae;
    --color-primary-variant: #4B886A;
    --color-white: ###E8F3F1;
    --color-light: #95B4AE;

    --transition:all 400ms ease;
   
    --container-width-lg:75%;
    --container-width-md:86%;
    --container-width-ms:90%
}

html{
    scroll-behavior: smooth;
}

::-webkit-scrollbar{
    display: none;
}
body{
    font-family: 'Poppins', sans-serif;
    background: var(--color-bg);
    /* color: var(--color-white); */
    line-height: 1.7;
    background-image: url(../src/img/paper.jpg);
}
/* General style */

.container{
    width: var(--container-width-lg);
    margin: 0 auto;
}

h1,h2,h3,h4,h5{
    font-weight: 500;
}
h1{
    font-size: 2.5rem;
}
section{
    margin-top: 8rem;
    height: 100vh;
}
section > h2, section >h5,section >h3{
    text-align: center;
    color: var(--color-light);
}
section > h2{
    color: var(--color-primary);
    margin-bottom: 3rem;
}
.text-light{
    color: var(--color-light);
}

a{
    color: var(--color-primary);
    transition: var(--transition);
}

a:hover{
    color: var(--color-white);
}

.btn{
    width: max-content;
    display: inline-block;
    color: var(--color-primary);
    padding: 0.75rem 1.2rem;
    border-radius: 20px;
    cursor: pointer;
    border: 1px solid var(--color-primary);
    transition: var(--transition);
    margin-left: 10px;
    
}
.btn:hover{
    color: white;
    background: #4B886A;
    box-shadow: 0 0 10px #4B886A, 0 0 40px #4B886A, 0 0 80px #4B886A;
    transition-delay: 0.1s;
}


.btn-primary{
    background: var(--color-primary);
    color: white
}
img{
    display: block;
    width: 100%;
    object-fit: cover;
}

@media screen and (max-width:600px){
    .container{
        width: var(--container-width-md);
    }
    section > h2{
        margin-bottom: 2rem;
    }
}

